import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

import en from '../languages/en.json';
import vi from '../languages/vi.json';

i18n.use(initReactI18next).init({
  lng: 'en',
  resources: {
    en: {
      common: en,
    },
    vi: {
      common: vi,
    },
  },
  ns: ['common'],
  defaultNS: 'common',
});

export { i18n };
