import { Box, Container, Grid, Paper, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import {
  Cake as CakeIcon,
  Email as EmailIcon,
  Home as HomeIcon,
  Phone as PhoneIcon,
} from '@material-ui/icons';
import moment from 'moment';
import React, { lazy, Suspense } from 'react';
import { useTranslation } from 'react-i18next';

import { Loading } from '../../components';
import { MY } from '../../constants';
import { Row, Social } from './components';

const Avatar = lazy(() => import('./components/Avatar'));

const useStyles = makeStyles((theme) => ({
  container: {
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(5),
  },
  paper: {
    padding: theme.spacing(5),
    boxShadow: theme.shadows[10],
  },
  name: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  icon: {
    color: theme.palette.primary.light,
  },
  detail: {
    marginTop: theme.spacing(),
  },
}));

const Home = () => {
  const { t } = useTranslation();
  const classes = useStyles();

  return (
    <Container className={classes.container} maxWidth="lg">
      <Paper className={classes.paper}>
        <Grid spacing={10} container>
          <Grid sm={6} xs={12} item>
            <Suspense fallback={<Loading />}>
              <Avatar />
            </Suspense>
          </Grid>
          <Grid sm={6} xs={12} item>
            <Typography>{t('my.hello')}</Typography>
            <Typography className={classes.name} variant="h3">
              {t('my.name')}
            </Typography>
            <Typography variant="h6">{t('my.job')}</Typography>
            <Typography className={classes.detail}>{t('my.detail')}</Typography>
            <Box mt={5}>
              <Row
                icon={<CakeIcon className={classes.icon} />}
                label={moment(MY.BIRTHDAY, 'YYYY').format('YYYY')}
              />
              <Row icon={<PhoneIcon className={classes.icon} />} label={MY.PHONE_NUMBER} />
              <Row icon={<EmailIcon className={classes.icon} />} label={MY.EMAIL} />
              <Row icon={<HomeIcon className={classes.icon} />} label={MY.ADDRESS} />
            </Box>
            <Box mt={5}>
              <Social />
            </Box>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
};

export default Home;
