import { AppBar, Box, Container, Toolbar, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { Link } from '../../components';
import { AppMaps } from '../../screens';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexDirection: 'row',
    flex: 1,
    justifyContent: 'space-between',
  },
  menu: {
    display: 'flex',
    flexDirection: 'row',
  },
  item: {
    margin: theme.spacing(),
  },
}));

const menus = [
  { title: 'title.home', path: AppMaps.home },
  { title: 'title.detail', path: AppMaps.detail },
  { title: 'title.contact', path: AppMaps.contact },
];

export const Header = () => {
  const classes = useStyles();
  const { t } = useTranslation();

  return (
    <AppBar position="sticky">
      <Container maxWidth="lg">
        <Toolbar className={classes.container}>
          <Box>
            <Link to={AppMaps.home}>
              <Typography>{t('my.name')}</Typography>
            </Link>
          </Box>
          <Box className={classes.menu}>
            {menus.map((menu) => (
              <Link key={menu.path} className={classes.item} to={menu.path}>
                <Typography>{t(menu.title)}</Typography>
              </Link>
            ))}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
