import React, { Fragment } from 'react';
import { Route } from 'react-router-dom';

import { MainLayout } from './MainLayout';

const PublicRoutes = ({ routes }) => {
  return (
    <Fragment>
      <MainLayout>
        {routes.map((route) => (
          <Route key={`${route.path}`} {...route} />
        ))}
      </MainLayout>
    </Fragment>
  );
};

export { PublicRoutes };
